html {
  height: 100%;
}

body {
  background: #FFF;
  line-height: 1.4;
  min-height: 100%;
  min-width: 1200px;
}

.out {
  min-height: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

main {
  width: 100%;
  margin-left: 280px;
}

section {
  min-height: calc(100vh - 175px);
}

.container {
  padding: 30px;
  padding-top: 40px;
  width: 1000px;
  margin: 0 auto;
}

.title {
  font-family: "montserrat-semibold", Arial, sans-serif;
  font-size: 24px;
  font-weight: 600;
  color: #000;
  line-height: 1.6;
}

@media only screen and (max-width: 1200px) {
  .title {
    font-size: 22px;
  }
}

@media only screen and (max-width: 768px) {
  .title {
    font-size: 18px;
  }
}

.subtitle {
  font-family: "montserrat-semibold", Arial, sans-serif;
  font-size: 18px;
  font-weight: 600;
  color: #000;
  line-height: 1.6;
}

@media only screen and (max-width: 1200px) {
  .subtitle {
    font-size: 17px;
  }
}

@media only screen and (max-width: 768px) {
  .subtitle {
    font-size: 16px;
  }
}

.text,
p {
  font-family: "open-sans-regular", Arial, sans-serif;
  font-size: 15px;
  line-height: 20px;
  color: #000;
}

@media only screen and (max-width: 768px) {
  .text,
  p {
    font-size: 14px;
  }
}

.label-checkbox {
  display: inline-block;
  font-family: "open-sans-regular", Arial, sans-serif;
  font-size: 15px;
  cursor: pointer;
  position: relative;
  padding: 10px;
  padding-left: 40px;
}

.checkbox[type=checkbox] {
  display: none;
}

.checkbox + .input-circle {
  display: inline-block;
  position: relative;
  text-align: center;
