.slick-dots button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  padding: 5px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}

.slick-dots button:before {
  position: absolute;
  content: '';
  width: 8px;
  height: 8px;
  border-radius: 100px;
  background: #D0D0D0;
  transition: all 500ms;
}

.slick-dots .slick-active button:before {
  width: 16px;
  background: #1380F6;
}

.slick-arrow {
  font-size: 0;
  background: #fff;
  display: block;
  position: relative;
  width: 40px;
  height: 40px;
  border-radius: 100px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.12);
  position: absolute;
  z-index: 10;
}

.slick-prev {
  display: block;
  top: 40%;
  left: -20px;
}

.slick-prev:before {
  position: absolute;
  content: '';
  width: 20px;
  height: 20px;
  top: 13px;
  left: 12px;
  background: url("../img/slider-prev.png") no-repeat;
}

.slick-next {
  display: block;
  top: 40%;
  right: -20px;
}

.slick-next:before {
  position: absolute;
  content: '';
  top: 14px;
  left: 12px;
